@import url(https://fonts.googleapis.com/css?family=Lato:400,700|Montserrat:400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
	--priceCalcSliderGreen: #078a7a;
}

html[data-theme='dark'] {
	--priceCalcSliderGreen: #66cdc1;
}

.range-description {
	font-size: 1.2em;
	line-height: 1.5em;
}
.range-description h2 {
	font-size: 18px;
	margin: 8px 0;
	/* line-height: 1.5em; */
}

.upper-portion {
	padding-bottom: 15px;
}

.dynamic-number-value {
	color: #078a7a;
	color: var(--priceCalcSliderGreen);
	margin: 0px 6px;
	font-weight: bold;
	border-bottom: 1px solid #078a7a;
	border-bottom: 1px solid var(--priceCalcSliderGreen);
	/* padding: 0px 6px 2px 6px; */
}

.range-slider__range {
	-webkit-appearance: none;
	width: 100%;
	height: 10px;
	border-radius: 5px;
	background: #d7dcdf;
	outline: none;
	padding: 0;
	margin: 0;
}

.range-slider__range::-webkit-slider-thumb {
	-webkit-appearance: none;
	appearance: none;
	width: 20px;
	height: 20px;
	border-radius: 50%;
	background: #ffffff;
	cursor: pointer;
	-webkit-transition: background 0.15s ease-in-out;
	transition: background 0.15s ease-in-out;
	box-shadow: 0 0 1px 3px rgba(0, 0, 0, 0.09);
}

.range-slider__range::-webkit-slider-thumb:hover {
	background: #f1f1f1;
}

.box-minmax-key {
	display: flex;
	justify-content: space-between;
	padding-top: 5px;
}

html {
  --priceCalcBG1: #035a99;
  --priceCalcBG2: #4179a0;
  --priceCalcCompBar: rgb(243, 243, 243);
  --priceCalcCompRed: #b10500;
  --priceCalcCompGreen: #639b17;
  --priceCalcSliderGreen: #0c4e7e;
  --cardBorderColor: #d0d0d0;
}

.bg {
  position: absolute;
  width: 100%;
  height: 80vh;
  background: #4179a0;
  background: var(--priceCalcBG2);
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#035a99), to(#4179a0));
  background: linear-gradient(to right, #035a99, #4179a0);
  background: -webkit-gradient(linear, left top, right top, from(var(--priceCalcBG1)), to(var(--priceCalcBG2)));
  background: linear-gradient(to right, var(--priceCalcBG1), var(--priceCalcBG2));
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 61%, 0 98%);
          clip-path: polygon(0 0, 100% 0, 100% 61%, 0 98%);
  top: 0;
  z-index: -1;
}

.range-slider-wrapper,
.competitor-wrapper,
.card-container {
  box-sizing: border-box;
  background-color: #fcfcfc;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  padding: 16px;
  border: 1px solid #d0d0d0;
  border: 1px solid var(--cardBorderColor);
}

.companies-table-title,
.competitor-container {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr 1fr 0.8fr;
      grid-template-columns: 1fr 1fr 1fr 0.8fr;
  padding: 10px 25px;
  margin: 0 -15px;
}

.competitor-commission-amt span {
  font-weight: bold;
  /* color: #0c4e7e; */
  padding: 6px 10px;
  margin: 0 3px;
  display: inline-block;
  min-width: 51px;
  line-height: 1.5em;
}

.range-widget-container {
  height: auto;
  height: initial;
  max-width: 1000px;
  width: 100%;
  padding: 5px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  color: var(--color-text);
}

.range-widget-container .intro-header {
  padding: 15px 0 45px 0;
  text-align: center;
  color: white;
}

.range-widget-container .intro-header h1 {
  display: flex;
  flex-direction: column;
  font-size: 2.5em;
}

.range-widget-container .intro-header .description {
  font-size: 1.1em;
  line-height: 1.5em;
  margin-top: 10px;
}

.range-widget-container .range-container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
}

.range-slider-wrapper {
  width: 48%;
  margin-bottom: 35px;
  box-sizing: border-box;
}

.companies-table-title h3 {
  font-size: 1em;
  font-weight: bold;
  text-align: left;
}

.competitor-wrapper {
  width: 100%;
}

.special-information {
  font-size: 10px !important;
  position: absolute !important;
  bottom: -13px;
  font-weight: 400 !important;
  opacity: 0.7;
  left: -3px;
  width: 190px !important;
}

.competitor-wrapper {
  width: 100%;
  /* display: flex; */
  position: relative;
}

.competitor-wrapper .companies {
  width: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.competitor-wrapper .competitor-container span {
  padding: 5px 0;
  font-size: 1em;
  text-align: left;
  position: relative;
}

.competitor-wrapper .competitor-container:nth-of-type(odd) {
  background: rgb(243, 243, 243);
  background: var(--priceCalcCompBar);
}

.range-description {
  font-size: 1em;
  text-align: left;
}

.competitor-company-commission-wrapper {
  width: 530px;
  display: flex;
  align-items: center;
  width: 100%;
}

.competitor-container span {
  font-size: 0.8em;
}

.competitor-company-commission-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.intro-header h1 {
  font-size: 1.8em;
}

.intro-header .description {
  font-size: 1em;
  line-height: 1.5em;
}

@media only screen and (max-width: 900px) {
  .companies-table-title,
  .competitor-container {
    -ms-grid-columns: 0.9fr 0.9fr 1.1fr 0.7fr;
        grid-template-columns: 0.9fr 0.9fr 1.1fr 0.7fr;
    padding: 15px 10px;
  }
  .competitor-container span {
    font-size: 0.8em;
  }
  .companies-table-title h3 {
    margin-bottom: -20px;
    padding-top: 10px;
  }
  .competitor-company-commission-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .range-slider-wrapper {
    width: 100%;
  }
  .companies-table-title h3 {
    font-size: 0.8em;
    /* white-space: nowrap; */
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
    -webkit-transform-origin: left;
            transform-origin: left;
    line-height: 14px;
  }
  .range-description {
    font-size: 1em;
  }
  .competitor-wrapper {
    font-size: 0.9em;
  }
  .intro-header h1 {
    font-size: 1.8em;
  }
  .intro-header .description {
    font-size: 1em;
    line-height: 1.5em;
  }
}
